import axios from "axios";
import { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import { Link } from "react-router-dom";

const AdminEarningLayer = () => {

  const [adminEarnings, setAdminEarnings] = useState([]);
  const fetchEarningsAdmin = async () => {
    try {
      const adminId = localStorage.getItem("adminId");
      if (adminId) {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/bookings/admin/stats-date`);
        setAdminEarnings(res.data);
      }
      else {
        console.error("No adminId in local Storage");
      }
    }
    catch (error) {
      console.error("Error in fetching data", error);

    }
  }

  const [monthlyEarnings,setMonthlyEarnings] = useState([]);
  const adminMonthlyEarnings = async () =>{
    try{
      const adminId = localStorage.getItem("adminId");
      if(adminId){
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/bookings/admin/monthly-earning`)
        setMonthlyEarnings(res.data.stats);
      }
      else{
        console.error("adminId cannot find in localStoarge");
      }
    }
    catch(error){
      console.error("Failed fetch the admin monthly earnings",error);  
    }
  }

  let options = {
  chart: {
    id: "monthly-earnings",
  },
  xaxis: {
    categories: 
      monthlyEarnings.map((item) =>item.month),
  },
};

let series = [
  {
    name: "Platform Fees",
    data: monthlyEarnings.map((item) =>item.platformFees),
  },
];

  useEffect(() => {
    fetchEarningsAdmin();
    adminMonthlyEarnings();
  }, []);

  return (
    <div className="dashboard-body__content">
      <div className="card common-card">
        <div className="card-body">
          <div className="row gy-4">
            <div className="col-lg-12">
              <div className="dashboard-card">
                <div className="dashboard-card__header">
                  <h6 className="dashboard-card__title mb-0">Monthly Earnings</h6>
                </div>
                <Chart
                  options={options}
                  series={series}
                  type="area"
                  width={"100%"}
                  height={"100%"}
                />
              </div>
            </div>

            <div className="col-lg-12">
              <div className="card common-card border shadow-none border-gray-five">
                <div className="card-body">
                    <div className="d-flex justify-content-end">
    <Link
      to="/earningSummary"
      className="btn btn-main pill"
      style={{ width: "150px" }}
    >
      Earning Summary
    </Link>
  </div>
                  <div className="table-responsive">
                    <table className="table text-body mt--24">
                      <thead>
                        <tr>
                          <th>Date</th>
                          <th>Completed Appointments</th>
                          <th>Earning</th>
                        </tr>
                      </thead>
                      <tbody>

                      {adminEarnings?.map((earnings,index) => (
                          <tr key={index}>
                          <td> {earnings?.date}</td>
                          <td> {earnings?.completedAppointments}</td>
                          <td> {earnings?.completedEarning}</td>
                        </tr>
                      ))}
                      

                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminEarningLayer;
